/* Base Styles */
body {
  font-family: 'Source Sans Pro', sans-serif;
  font-size: 16px;
  line-height: 1.7;
  color: #222;
}

/* Container */
.app-container {
  padding: 2rem;
  max-width: 1000px;
  margin: 0 auto;
}

/* Product Display */
.product {
  margin-bottom: 5rem;
  background: white;
  padding: 2rem;
  border-radius: 12px;
  box-shadow: 0 0 20px rgba(66, 38, 38, 0.04);
}

.product img {
  max-width: 300px;
  width: 100%;
  height: auto;
  display: block;
  margin-bottom: 1rem;
}

.product h2 {
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
}

/* Related Products */
.related {
  display: flex;
  flex-wrap: wrap;
  gap: 2rem;
  margin-top: 2rem;
}

.related a {
  text-decoration: none;
  color: inherit;
  text-align: center;
  transition: transform 0.2s;
}

.related a:hover {
  transform: scale(1.05);
}

/* Navigation Buttons */
.nav-btn {
  background-color: black;
  padding: 0;
  width: 48px;
  height: 48px;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  box-shadow: none;
}

.nav-btn img {
  width: 22px;
  height: 22px;
  object-fit: contain;
}

.cart-btn {
  position: relative;
}

.cart-count {
  position: absolute;
  top: -6px;
  right: -6px;
  background-color: white;
  color: black;
  font-size: 0.7rem;
  font-weight: bold;
  padding: 2px 6px;
  border-radius: 999px;
  border: 1px solid black;
  line-height: 1;
}