.cart-drawer {
  position: fixed;
  top: 0;
  right: 0;
  width: 400px;
  max-width: 90vw;
  height: 100%;
  background-color: #fff;
  box-shadow: -2px 0 5px rgba(0, 0, 0, 0.1);
  transform: translateX(100%);
  transition: transform 0.3s ease;
  z-index: 2000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
}

.cart-drawer.open {
  transform: translateX(0);
}

.cart-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 1.25rem;
  font-weight: 700;
  border-bottom: 1px solid #ddd;
}

.close-btn {
  background: none;
  border: none;
  font-size: 1.5rem;
  color: #333;
  cursor: pointer;
  transition: color 0.2s ease;
}

.close-btn:hover {
  color: black;
}

.shipping-banner {
  padding: 1rem 1.25rem 0.5rem;
  font-size: 0.95rem;
  border-bottom: 1px solid #eee;
}

.shipping-banner strong {
  color: #000;
}

.shipping-bar {
  width: 100%;
  height: 6px;
  background: #ddd;
  border-radius: 4px;
  margin-top: 0.5rem;
  overflow: hidden;
}

.shipping-bar .fill {
  height: 100%;
  background-color: black;
  transition: width 0.3s ease;
}

.cart-items {
  padding: 1.25rem;
}

.cart-item {
  display: flex;
  gap: 1rem;
  margin-bottom: 1.5rem;
  align-items: flex-start;
}

.cart-item img {
  width: 70px;
  height: 70px;
  object-fit: cover;
  border-radius: 6px;
}

.details {
  flex: 1;
  font-size: 0.9rem;
}

.details p {
  margin: 0.25rem 0;
}

.details button {
  margin-top: 0.5rem;
  font-size: 0.8rem;
  padding: 0.4rem 0.8rem;
  background-color: #f5f5f5;
  border: 1px solid #ccc;
  border-radius: 4px;
  cursor: pointer;
}

.details button:hover {
  background-color: #eee;
}

.suggested-products {
  padding: 1rem 1.25rem;
  border-top: 1px solid #eee;
}

.suggested-products h3 {
  font-size: 1rem;
  margin-bottom: 0.75rem;
  font-weight: 600;
}

.suggested-list {
  display: flex;
  gap: 1rem;
}

.suggested-item {
  width: 60px;
  height: 60px;
  border-radius: 6px;
  background: #f0f0f0;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 0.7rem;
  text-align: center;
  cursor: pointer;
}

.cart-footer {
  margin-top: auto;
  padding: 1.25rem;
  border-top: 1px solid #eee;
}

.cart-footer p {
  font-weight: bold;
  margin-bottom: 1rem;
}

.cart-footer button {
  width: 100%;
  background-color: black;
  color: white;
  border: none;
  padding: 0.9rem;
  font-weight: bold;
  cursor: pointer;
  font-size: 1rem;
  border-radius: 6px;
  transition: background-color 0.2s ease;
}

.cart-footer button:hover {
  background-color: #222;
}