/* Checkout.css */

.checkout-container {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding: 3rem;
  gap: 4rem;
  background-color: #f9f9f9;
  flex-wrap: wrap;
}

.checkout-left {
  background-color: #fff;
  padding: 2rem;
  border-radius: 12px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.05);
  flex: 1 1 400px;
  min-width: 350px;
  max-width: 500px;
}

.checkout-right {
  background-color: #fff;
  padding: 2rem;
  border-radius: 12px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.05);
  flex: 1 1 300px;
  min-width: 300px;
  max-width: 400px;
}

h2 {
  font-size: 1.5rem;
  margin-bottom: 1rem;
}

form label {
  display: block;
  margin-top: 1rem;
  font-weight: 600;
}

form input {
  width: 100%;
  padding: 0.75rem;
  margin-top: 0.5rem;
  border: 1px solid #ccc;
  border-radius: 6px;
  font-size: 1rem;
}

.checkout-summary {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.summary-item {
  display: flex;
  justify-content: space-between;
  font-size: 1rem;
}

.total {
  font-weight: bold;
  font-size: 1.25rem;
  margin-top: 1rem;
  display: flex;
  justify-content: space-between;
}

.checkout-product {
  display: flex;
  align-items: center;
  gap: 1rem;
  border-top: 1px solid #eee;
  padding-top: 1rem;
}

.checkout-product img {
  width: 60px;
  height: 60px;
  object-fit: cover;
  border-radius: 8px;
}

.checkout-product-info {
  font-size: 0.9rem;
}

.pay-now-btn {
  margin-top: 2rem;
  width: 100%;
  padding: 1rem;
  background-color: black;
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 1rem;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.2s;
}

.pay-now-btn:hover {
  background-color: #333;
}